import { mapState, mapActions } from 'vuex';
import { debounce } from 'debounce';
import utils from '@/utils';

export default {
  name: 'MainPage',
  async fetch({ store }) {
    await store.dispatch('News/resetNews');
    await store.dispatch('News/getTopicNews');
    await store.dispatch('News/getNewsCategories');
    await store.dispatch('News/getNews', {
      category: '',
      keyword: '',
      perPage: 6,
      page: 1,
    });
  },
  data() {
    return {
      searchText: '',
      breadData: [
        {
          name: 'LOL__BREADCRUMB_PAGE_NAME__INDEX',
          url: '/',
        },
        {
          name: 'LOL__BREADCRUMB_PAGE_NAME__NEWS',
          url: '/news',
        },
      ],
      swiperOptions: {
        slidesPerView: 'auto',
        spaceBetween: 15,
        slidesPerViewFit: true,
        roundLengths: true,
        calculateHeight: true,
      },
      // reachFixHeight: false,
      overFixHeight: 0,
    };
  },
  computed: {
    ...mapState('News', [
      'topicNewsLoading',
      'topicNews',
      'firstTimeLoading',
      'refetchLoading',
      'news',
      'page',
      'next',
      'categoriesLoading',
      'categories',
    ]),
    swiper() {
      return this.$refs.mySwiper.$swiper;
    },
  },
  mounted() {
    if (!this.firstTimeLoading)
      window.addEventListener('scroll', this.scrollToFetchNews);
  },
  destroyed() {
    window.removeEventListener('scroll', this.scrollToFetchNews);
  },
  methods: {
    ...mapActions('News', [
      'resetNews',
      'getTopicNews',
      'getNews',
      'getMoreNews',
      'getNewsCategories',
    ]),
    searchNews() {
      this.resetNews();
      this.getNews({
        category: '',
        keyword: this.searchText,
        perPage: 6,
        page: this.page,
      });
    },
    scrollToFetchNews: debounce(function () {
      if (!this.next) {
        window.removeEventListener('scroll', this.scrollToFetchNews);
      } else {
        const target = document.getElementById('news');
        if (target) {
          const newsHeight = target.offsetHeight + target.offsetTop;
          if (utils.getScrollTop() + utils.getClientHeight() >= newsHeight) {
            this.getMoreNews({
              category: '',
              keyword: this.searchText,
              perPage: 6,
            });
          }
        }
      }
    }, 300),
  },
};
